// @todo enable the following disabled rules see OPENTOK-31136 for more info
/* eslint-disable no-shadow, array-callback-return */


const MediaStreamTrack = require('./media_stream_track.js');
const VideoContainer = require('./video_container.js');

const MediaStream = function (options, plugin) {
  const Proto = function MediaStream() {};
  const mediaStream = new Proto();
  const audioTracks = [];
  const videoTracks = [];

  mediaStream.id = options.id;
  plugin.refCounter.add(mediaStream);

  // TODO
  // mediaStream.ended =
  // mediaStream.onended =

  if (options.videoTracks) {
    options.videoTracks.map((track) => {
      videoTracks.push(new MediaStreamTrack(options.id, track, plugin));
    });
  }

  if (options.audioTracks) {
    options.audioTracks.map((track) => {
      audioTracks.push(new MediaStreamTrack(options.id, track, plugin));
    });
  }

  const hasTracksOfType = function (type) {
    const tracks = type === 'video' ? videoTracks : audioTracks;

    return tracks.some(track => track.enabled);
  };

  mediaStream.getVideoTracks = function () { return videoTracks; };
  mediaStream.getAudioTracks = function () { return audioTracks; };
  mediaStream.getTracks = function () { return videoTracks.concat(audioTracks); };

  mediaStream.getTrackById = function (id) {
    videoTracks.concat(audioTracks).forEach((track) => {
      if (track.id === id) {
        return track;
      }

      // FIXME: I added this line to satisfy consistent-return in eslint, but I think this whole
      // method doesn't work / doesn't make sense / must not be tested.
      return undefined;
    });

    return null;
  };

  mediaStream.hasVideo = function () {
    return hasTracksOfType('video');
  };

  mediaStream.hasAudio = function () {
    return hasTracksOfType('audio');
  };

  mediaStream.addTrack = function (/* MediaStreamTrack */) {
    // TODO
  };

  mediaStream.removeTrack = function (/* MediaStreamTrack */) {
    // TODO
  };

  mediaStream.stop = function () {
    plugin._.stopMediaStream(mediaStream.id);
    plugin.refCounter.remove(mediaStream);
  };

  mediaStream.destroy = function () {
    mediaStream.stop();
  };

  // Private MediaStream API
  mediaStream._ = {
    plugin,

    // Get a VideoContainer to render the stream in.
    render() {
      return new VideoContainer(plugin, mediaStream);
    },
  };

  return mediaStream;
};

const mediaStreams = {};
MediaStream.fromJson = function (json, plugin) {
  if (!json) {
    return null; // TODO: This seems odd. Consider removing/throwing/logging.
  }
  const ms = new MediaStream(JSON.parse(json), plugin);
  mediaStreams[ms.id] = ms;
  return ms;
};

MediaStream.getOrCreate = function (json, plugin) {
  if (!json) {
    return null; // TODO: This seems odd. Consider removing/throwing/logging.
  }
  const msObj = JSON.parse(json);
  const ms = mediaStreams[msObj.id];
  if (ms) {
    return ms;
  }
  return MediaStream.fromJson(json, plugin);
};

module.exports = MediaStream;



