function checkPeerConnection(pc) {
  return new Promise((resolve) => {
    pc.addEventListener('signalingstatechange', () => resolve(true));
    pc.createOffer({ offerToReceiveAudio: 1 })
      .then(offer => pc.setLocalDescription(offer))
      .then(() => setTimeout(() => { resolve(false); }, 1000));
  });
}

function hasValidPeerConnection(PeerConnection) {
  let pc;

  try {
    pc = new PeerConnection({ iceServers: [] });
  } catch (err) {
    console.error('Error occurred creating PeerConnection', err);
    return Promise.resolve(false);
  }

  return checkPeerConnection(pc)
    .catch((err) => {
      console.error('Error occurred checking the validity of the PeerConnection', err);
      return false;
    })
    .then((result) => {
      try {
        pc.close();
      } catch (err) {
        console.error('Error occurred closing PeerConnection', err);
      }
      return result;
    });
}

module.exports = hasValidPeerConnection;



